

































import Vue from "vue";
import Component from "vue-class-component";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast } from "vant";

@Component({
  components: {  },
  mixins: [mixin]
})
export default class messagelist extends Vue {

  handleContent(item){
    if(item.type == 'ORDER_MSG'){
    this.goOrderDetail(item)
    }
    if(item.type == 'AWARD_MSG'){
      this.goReward()
    }
    if(item.type == 'MEMBER_MSG'){
      this.goMember()
    }


  }
  messagelist=[];
  orderId="";
  lookMesge="";
    pageSize = 20;
  page = 0;
  total = 0;
    onPageChange(page) {
    this.page = page - 1;
    this.getList();
  }

  getList() {
    Vue.prototype.$reqFormPost("/message/list", {
      
          userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
            .userId,
          token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
            .token,
          page: this.page,
          pageSize: 20
    }, res => {
          if (res == null) {
            console.log("网络请求错误！");
            return;
          }
          if (res.data.status != 200) {
            console.log(
              "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
            );
            Toast(res.data.message);
            return;
          }
          console.log(res.data.data)
        this.messagelist = res.data.data.messageList;
        this.total = res.data.data.page.total
        });
  }
  //未阅读消息
  unGetList() {
    console.log('消息',this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
            .userId)
    console.log('消息',this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
            .token)
    Vue.prototype.$reqFormPost("/message/unread/count", {
          userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
            .userId,
          token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
            .token,
          // page: this.page,
          // pageSize: 20
    }, res => {
          if (res == null) {
            console.log("网络请求错误！");
            return;
          }
          if (res.data.status != 200) {
            console.log(
              "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
            );
            Toast(res.data.message);
            return;
          }
          
          this.messagelist = res.data.data.messageList;
        });
}
 goOrderDetail(item) {
   console.log('查看详情')
    this.$router.push({
      name: "orderdetail",
      query: {
        orderId: item.actionValue
      }
    });
  }
  goReward(){
    console.log('积分详情')
    this.$router.push({
      name:"my_reward"
    })
  }
  goMember(){
    this.$router.push({
      name:"my_member"
    })
  }
  mounted() {
    this.getList();
    this.unGetList();
    this.$emit('selectMenu',{
      name: '消息通知',
      url:'/messagelist',
    })
  }
}



